@import "./base/reset";
@import "./base/base";
@import "./base/vars";

.wrapper {
  max-height: 100dvh;
  display: flex;
  flex-direction: row;
}

.left-side-menu {
  flex-basis: 250px;
  flex-shrink: 0;
  flex-grow: 0;

  background-color: $color-green-3;
  color: $color-white;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .site-nav {
    display: flex;
    flex-direction: column;
  }

  .account {
    padding: 16px;
  }

  @media screen and (max-width: 560px) {
    flex-basis: 160px;
  }
}

.main {
  flex-grow: 1;
  overflow: hidden;

  padding: 30px;

  @media screen and (max-width: 560px) {
    padding: 16px 5px;
  }
}

.site-logo {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-weight: 600;
  padding: 16px;
}

.site-nav {
  &__link {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    padding: 20px;
    background-color: $color-green-3;
    transition: all 0.3s ease-in-out;
    outline: none;

    &:hover {
      background-color: lighten($color-green-3, 5%);
    }

    &.active {
      background-color: lighten($color-green-3, 5%);
    }

    // &:target {
    //     background-color: lighten($color-green-3, 5%);
    // }
  }
}

.account-logout {
  cursor: pointer;
  padding: 8px 16px;
  display: flex;
  column-gap: 10px;
  margin: 0 auto;
  align-items: center;
  border: none;
  border-radius: 7px;
  background-color: red;
  font-weight: 600;
  font-size: 18px;
  color: white;
  transition: all 0.3s ease-in-out;

  &:hover {
    scale: 0.96;
    background-color: lighten(red, 5%);
  }
}

.tab-content {
  height: 100%;
}

.categories {
  display: none;

  &-wrapper {
    border: 1px solid black;
    border-radius: 10px;
    padding: 20px;
    background-color: darken(grey, 10%);
    color: white;
  }

  &__text {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 15px 10px;
    margin-bottom: 20px;
  }

  &__list-item {
    width: fit-content;
    padding: 7px 14px;
    font-size: 18px;
    background-color: $color-green-4;
    border-radius: 7px;
    font-weight: 500;
    color: $color-white;
  }

  &__button {
    padding: 5px;
    display: flex;
    align-items: center;
    column-gap: 5px;
    border: none;
    background-color: transparent;
    color: inherit;

    &:hover {
      cursor: pointer;
      color: lighten($color-green-1, 30%);
    }
  }
}

.transactions {
  display: none;

  &-top {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 20px;
    column-gap: 20px;
  }

  &-form {
    display: flex;
    flex-direction: column;
  }

  &__form-wrapper {
    padding: 25px;
    border-radius: 8px;
    background-color: darken(grey, 10%);
    max-width: 700px;
    color: white;

    flex-grow: 1;

    @media screen and (max-width: 560px) {
      padding: 12px;
    }
  }

  &__form-label {
    display: flex;
    flex-direction: column;
    row-gap: 3px;
    margin-bottom: 12px;
    color: white;
  }

  &__form-type {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    position: relative;

    label {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &__form-input {
    background-color: lighten(#000, 50%);
    border: 1px solid black;
    border-radius: 6px;
    padding: 8px;

    &::placeholder {
      color: black;
    }
  }

  &-form__button {
    cursor: pointer;
    padding: 10px 13px;
    font-weight: 600;
    font-size: 18px;
    color: white;
    border: none;
    border-radius: 5px;
    width: fit-content;
    background-color: $color-green-3;

    &:hover {
      background-color: lighten($color-green-3, 5%);
    }
  }

  .categories-setup {
    margin-bottom: 10px;

    & ~ p {
      margin-bottom: 8px;
    }
  }

  &__totals {
    flex-shrink: 0;
    padding: 25px;
    border-radius: 8px;
    background-color: darken(grey, 10%);
    max-width: 300px;
    width: 100%;

    @media screen and (max-width: 560px) {
      flex-shrink: 1;
      padding: 12px;
    }
  }

  &__totals-wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
  }

  .total-income__text,
  .total-expense__text {
    text-transform: uppercase;
    font-weight: 700;
    color: white;
    margin-bottom: 12px;

    @media screen and (max-width: 560px) {
      font-size: 12px;
    }
  }

  .total-income__amount,
  .total-expense__amount {
    font-weight: 500;
    text-align: center;
    padding: 6px;
    color: white;
  }

  .total-income__amount {
    background-color: lighten(green, 10%);
  }

  .total-expense__amount {
    background-color: lighten(red, 10%);
  }
}

.categories-modal {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;

  .modal-content {
    background-color: $color-green-1;
    padding: 25px;
    border-radius: 10px;
  }

  .add-category,
  .add-category__text {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
  }

  .add-category__text {
    color: white;
    font-size: 20px;
  }

  .add-category__input {
    background-color: lighten(#000, 30%);
    padding: 5px;
    border: 1px solid rgba(0, 0, 0, 0.7);
    border-radius: 7px;
  }

  .add-category__button {
    cursor: pointer;
    padding: 6px 10px;
    border-radius: 5px;
    border: none;
  }

  #save-category {
    background-color: lighten(green, 12%);
  }

  #close-modal {
    background-color: lightcoral;
  }
}

#myChart {
  max-width: 300px;
  max-height: 300px;
}

#pagination {
  display: flex;
  flex-wrap: wrap;
  row-gap: 15px;
  justify-content: center;
  margin-bottom: 20px;
}

.page-link {
  margin: 0 5px;
  cursor: pointer;
}

.transactions-bottom {
  padding-top: 20px;
  #transactions-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .transactions-page {
    font-family: 'Courier New', monospace;
    width: 40px;
    height: 40px;
    box-sizing: border-box;
    font-weight: 600;
    border: none;
    cursor: pointer;
    background-color: lighten(#676767, 40%);
    transition: background-color .4s ease-in-out;

    &:hover {
      background-color: lighten(#676767, 25%);
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}

#transactionsTable {
  width: 75%;
  table-layout: fixed;
  border-collapse: collapse;

  thead th {
    font-weight: 700;
    font-size: 18px;
    color: #676767;
    background-color: rgba(#000, 0.2);
  }

  th,
  td {
    padding: 10px;
    text-align: center;
    border-right: 1px solid #676767;
    border-left: 1px solid #676767;
  }

  tbody td {
    font-weight: 500;
    background-color: rgba(#000, 0.1);
  }

  .transaction-date {
    text-wrap: nowrap;
    overflow: hidden;
  }

  .delete-transaction {
    cursor: pointer;
  }
}
