html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button,
input {
  font: inherit;
}

body {
  font-family: "Chakra Petch", sans-serif;
  font-size: 16px;
  font-weight: 400;
  box-sizing: border-box;
  height: 100dvh;
}

.wrapper {
  box-sizing: border-box;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.header,
.footer {
  flex-grow: 0;
  flex-shrink: 0;
}

.main {
  flex-grow: 1;
}

.wrapper {
  max-height: 100dvh;
  display: flex;
  flex-direction: row;
}

.left-side-menu {
  flex-basis: 250px;
  flex-shrink: 0;
  flex-grow: 0;
  background-color: #0c9746;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.left-side-menu .site-nav {
  display: flex;
  flex-direction: column;
}
.left-side-menu .account {
  padding: 16px;
}
@media screen and (max-width: 560px) {
  .left-side-menu {
    flex-basis: 160px;
  }
}

.main {
  flex-grow: 1;
  overflow: hidden;
  padding: 30px;
}
@media screen and (max-width: 560px) {
  .main {
    padding: 16px 5px;
  }
}

.site-logo {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-weight: 600;
  padding: 16px;
}

.site-nav__link {
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  padding: 20px;
  background-color: #0c9746;
  transition: all 0.3s ease-in-out;
  outline: none;
}
.site-nav__link:hover {
  background-color: #0eaf51;
}
.site-nav__link.active {
  background-color: #0eaf51;
}

.account-logout {
  cursor: pointer;
  padding: 8px 16px;
  display: flex;
  column-gap: 10px;
  margin: 0 auto;
  align-items: center;
  border: none;
  border-radius: 7px;
  background-color: red;
  font-weight: 600;
  font-size: 18px;
  color: white;
  transition: all 0.3s ease-in-out;
}
.account-logout:hover {
  scale: 0.96;
  background-color: #ff1a1a;
}

.tab-content {
  height: 100%;
}

.categories {
  display: none;
}
.categories-wrapper {
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
  background-color: #676767;
  color: white;
}
.categories__text {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}
.categories__list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px 10px;
  margin-bottom: 20px;
}
.categories__list-item {
  width: fit-content;
  padding: 7px 14px;
  font-size: 18px;
  background-color: #5ecf4a;
  border-radius: 7px;
  font-weight: 500;
  color: #fff;
}
.categories__button {
  padding: 5px;
  display: flex;
  align-items: center;
  column-gap: 5px;
  border: none;
  background-color: transparent;
  color: inherit;
}
.categories__button:hover {
  cursor: pointer;
  color: #26d795;
}

.transactions {
  display: none;
}
.transactions-top {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 20px;
  column-gap: 20px;
}
.transactions-form {
  display: flex;
  flex-direction: column;
}
.transactions__form-wrapper {
  padding: 25px;
  border-radius: 8px;
  background-color: #676767;
  max-width: 700px;
  color: white;
  flex-grow: 1;
}
@media screen and (max-width: 560px) {
  .transactions__form-wrapper {
    padding: 12px;
  }
}
.transactions__form-label {
  display: flex;
  flex-direction: column;
  row-gap: 3px;
  margin-bottom: 12px;
  color: white;
}
.transactions__form-type {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  position: relative;
}
.transactions__form-type label {
  display: flex;
  justify-content: center;
  align-items: center;
}
.transactions__form-input {
  background-color: gray;
  border: 1px solid black;
  border-radius: 6px;
  padding: 8px;
}
.transactions__form-input::placeholder {
  color: black;
}
.transactions-form__button {
  cursor: pointer;
  padding: 10px 13px;
  font-weight: 600;
  font-size: 18px;
  color: white;
  border: none;
  border-radius: 5px;
  width: fit-content;
  background-color: #0c9746;
}
.transactions-form__button:hover {
  background-color: #0eaf51;
}
.transactions .categories-setup {
  margin-bottom: 10px;
}
.transactions .categories-setup ~ p {
  margin-bottom: 8px;
}
.transactions__totals {
  flex-shrink: 0;
  padding: 25px;
  border-radius: 8px;
  background-color: #676767;
  max-width: 300px;
  width: 100%;
}
@media screen and (max-width: 560px) {
  .transactions__totals {
    flex-shrink: 1;
    padding: 12px;
  }
}
.transactions__totals-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
}
.transactions .total-income__text,
.transactions .total-expense__text {
  text-transform: uppercase;
  font-weight: 700;
  color: white;
  margin-bottom: 12px;
}
@media screen and (max-width: 560px) {
  .transactions .total-income__text,
  .transactions .total-expense__text {
    font-size: 12px;
  }
}
.transactions .total-income__amount,
.transactions .total-expense__amount {
  font-weight: 500;
  text-align: center;
  padding: 6px;
  color: white;
}
.transactions .total-income__amount {
  background-color: #00b300;
}
.transactions .total-expense__amount {
  background-color: #ff3333;
}

.categories-modal {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.categories-modal .modal-content {
  background-color: #0f553b;
  padding: 25px;
  border-radius: 10px;
}
.categories-modal .add-category,
.categories-modal .add-category__text {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
.categories-modal .add-category__text {
  color: white;
  font-size: 20px;
}
.categories-modal .add-category__input {
  background-color: #4d4d4d;
  padding: 5px;
  border: 1px solid rgba(0, 0, 0, 0.7);
  border-radius: 7px;
}
.categories-modal .add-category__button {
  cursor: pointer;
  padding: 6px 10px;
  border-radius: 5px;
  border: none;
}
.categories-modal #save-category {
  background-color: #00bd00;
}
.categories-modal #close-modal {
  background-color: lightcoral;
}

#myChart {
  max-width: 300px;
  max-height: 300px;
}

#pagination {
  display: flex;
  flex-wrap: wrap;
  row-gap: 15px;
  justify-content: center;
  margin-bottom: 20px;
}

.page-link {
  margin: 0 5px;
  cursor: pointer;
}

.transactions-bottom {
  padding-top: 20px;
}
.transactions-bottom #transactions-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.transactions-bottom .transactions-page {
  font-family: "Courier New", monospace;
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  font-weight: 600;
  border: none;
  cursor: pointer;
  background-color: #cdcdcd;
  transition: background-color 0.4s ease-in-out;
}
.transactions-bottom .transactions-page:hover {
  background-color: #a7a7a7;
}
.transactions-bottom .transactions-page:not(:last-child) {
  margin-right: 10px;
}

#transactionsTable {
  width: 75%;
  table-layout: fixed;
  border-collapse: collapse;
}
#transactionsTable thead th {
  font-weight: 700;
  font-size: 18px;
  color: #676767;
  background-color: rgba(0, 0, 0, 0.2);
}
#transactionsTable th,
#transactionsTable td {
  padding: 10px;
  text-align: center;
  border-right: 1px solid #676767;
  border-left: 1px solid #676767;
}
#transactionsTable tbody td {
  font-weight: 500;
  background-color: rgba(0, 0, 0, 0.1);
}
#transactionsTable .transaction-date {
  text-wrap: nowrap;
  overflow: hidden;
}
#transactionsTable .delete-transaction {
  cursor: pointer;
}