body {
  font-family: "Chakra Petch", sans-serif;
  font-size: 16px;
  font-weight: 400;
  box-sizing: border-box;
  height: 100dvh;
}

.wrapper {
  box-sizing: border-box;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.header,
.footer {
    flex-grow: 0;
    flex-shrink: 0;
}

.main {
  flex-grow: 1;
}
